QACI,+BAA+B;AACnC;AAEA;QACI,iCAAiC;AACrC;AACJ;AAEA;AACI;QACI,4BAA4B;AAChC;AAEA;QACI,+BAA+B;AACnC;AACJ;AAEA;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,kCAAkC;AACtC;AACJ;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,qBAAqB;IACrB,uBAAuB;IACvB,mCAAmC;IACnC,kCAAkC;IAClC,iBAAiB;AACrB;AAGA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;IACnB,gBAAgB;IAChB,eAAe;IACf,cAAc;AAClB;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,YAAY;AAChB;AAEA;IACI,qBAAqB;AACzB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from \"@vue/reactivity\";\nimport \"bootstrap/js/dist/dropdown\";\nimport { defineAsyncComponent, onMounted, ref } from 'vue';\nimport { IPlatformSelection } from \"../../../../../../common/qCloudTemp/exporter\";\nimport { ICustomLivePreview } from '../../../../../../common/qCloudTemp/livePreview';\nimport { ContentType, IQJsonAdditionals } from \"../../../../../../common/qCloudTemp/quickCloud\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IQRCode } from \"../../../../domain/infrastructure/IQRCode\";\nimport { IStreamingLivePreviewManager } from \"../../../../domain/objects/IStreamingLivePreviewManager\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\nimport { IPage } from \"../../../../domain/presentation/IPage\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IApplicationSettingsUseCase } from \"../../../../domain/useCase/IApplicationSettingsUseCase\";\nimport { IAuthenticationUseCase } from \"../../../../domain/useCase/IAuthenticationUseCase\";\nimport { IExportAppUseCase } from \"../../../../domain/useCase/IExportAppUseCase\";\nimport { ILivePreviewUseCase } from \"../../../../domain/useCase/ILivePreviewUseCase\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { IViewModelManager } from \"../../../../domain/viewModel/IViewModelManager\";\nimport { ITab } from \"../../../vue3/components/ui/ITabs\";\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport PremiumAccountTooltip from \"../../components/dialogs/premium/premiumAccountTooltip.vue\";\nimport AnnouncementsBar from \"../announcementsBar.vue\";\nimport CheckboxComp from \"../checkboxComp.vue\";\nimport ChatBot from \"../dialogs/chatbot.vue\";\nimport RadioButtonComp from \"../radioButtonComp.vue\";\n\n\nimport { IModelExtractorUseCase } from \"../../../../domain/useCase/IModelExtractor\";\nimport { IWorkflowViewUseCase, IProcess } from \"../../../../domain/useCase/IWorkflowViewUseCase\";\nimport { IUser } from \"../../../../../../common/qCloudTemp/runtimeDtoObjects\";\nimport { IModel } from \"../../../../domain/model/models\";\n\ndeclare global {\n    interface Window {\n        a: {\n            getModelByID: (id: string) => object;\n        };\n        workflow: IWorkflowViewUseCase\n    }\n}\n\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst page = container.resolve<IPage>(IPage);\nconst workflowView = container.resolve<IWorkflowViewUseCase>(IWorkflowViewUseCase);\n\nconst user = viewModel.user!;\n\nconst livePreviwUseCase = container.resolve<ILivePreviewUseCase>(ILivePreviewUseCase);\nconst streamingLpManager = container.resolve<IStreamingLivePreviewManager>(IStreamingLivePreviewManager);\nconst exportAppUseCase = container.resolve<IExportAppUseCase>(IExportAppUseCase);\n\ntype ExportType = \"create\" | \"sync\";\nconst router = container.resolve<IRouter>(IRouter);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst hoverValue = ref(false);\nconst previewType = ref<string>(\"default\");\nconst exportAppType = ref<ExportType>(\"create\");\nconst showCustomPreview = ref(false);\nconst previewDropdownOpen = ref(false);\nconst showQRCode = ref(false);\nconst showLoading = ref(true);\nconst customPreviewLastUpdated = ref();\nconst publishToWeb = ref(false);\nconst publishToMobile = ref(false);\nconst currentApp = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\nconst studioAppLink = ref(\"\");\nconst relayClients = computed(() => viewModel.studio.livePreview?.relayClients);\n\nconst isFlowProcess = computed(() => viewModel.studio.items.some(model => model.objectType === 'model' && model.modelType === \"flowProcess\"));\n\n\n\n\n// var autoDisconnect = applicationPreviewManager.getAutoDisconnect();\n\nfunction StopClientRelay(clientID: string) {\n    streamingLpManager.StopRelay(clientID);\n};\nconst isBackOfficeAvailable = computed(() => !!viewModel.user?.userRoles?.find(i => i.name == \"superAdmin\"));\n\nconst hasModel = computed(() => {\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return item.objectType == \"model\";\n    });\n});\n\nconst hasQJson = computed(() => {\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return item.objectType == \"model\" && item.modelType == \"qjson\";\n    });\n});\n\nconst hasRootPage = computed(() => {\n    // const appSettingsModel = appSettingsUseCase.getStartScreen();\n    // return !!appSettingsModel;\n\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return !!(item.objectType == \"model\" && item.modelType == \"qjson\" && (item.additionals as IQJsonAdditionals).isRootPage);\n    });\n});\n\nconst isPremiumTooltipActive = computed(() => !viewModel.user?.selectedOrganization?.accountType || viewModel.user?.selectedOrganization?.accountType != 'premium');\n\nconst isDeployAllowed = computed(() => viewModel.user?.selectedOrganization?.cloud);\n\nconst operationState = {\n    publishing: ref(false),\n    downloading: ref(false),\n}\n\nonMounted(() => {\n    document.getElementById(\"ulDropdown\")?.addEventListener(\"click\", (event: any) => {\n        if (event.target.id == \"custom-preview\") return;\n        event.stopPropagation();\n    })\n    document.getElementById(\"ulDropdownExport\")?.addEventListener(\"click\", (event: any) => {\n        if (event.target.id == \"see-progress-details\") return;\n        event.stopPropagation();\n    })\n    document.addEventListener(\"click\", () => {\n        const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")?.getAttribute(\"aria-expanded\");\n        previewDropdownOpen.value = ariaExpanded == \"true\";\n    })\n})\n\nfunction getUserName() {\n    const user = viewModel.user!;\n    if (user.name && user.lastName) { return `${user.name} ${user.lastName}`; }\n    return user.email;\n}\n\nfunction goToProfile() {\n    router.push(\"/completeProfile\");\n}\n\nasync function goToPreDashboard() {\n    viewModel.studio.workflowPreview = {};\n    // const app = viewModel.apps.find(app => app.ID == viewModel.studio.appId)\n    workflowView.setModelToDB?.();\n    await executor.execute(async () => {\n        // const modelObj : IModel = {\n        //   objectType: \"model\",\n        //   parent: app!,\n        //   modelType: \"appSettings\",\n        //   modified: {},\n        //   ID: \"1234567\",\n        //   name: \"elifnailmodel\",\n        //   synced: false\n        // }\n        // studio.openItem(modelObj, {fromHistory: true});\n        const newWindow = window.open(`../../processlivepreview/predashboard`, \"_blank\")\n        if (newWindow) {\n            newWindow.workflow = workflowFunctions();\n        }\n    }, { loading: true });\n}\nfunction workflowFunctions() : IWorkflowViewUseCase {\n    return {\n        async listUsers() {\n            return await workflowView.listUsers();\n        },\n        listUserRoles() {\n            return workflowView.listUserRoles();\n        },\n        listSwimlanes() {\n            return workflowView.listSwimlanes();\n        },\n        async insertUser(userInfo: IUser){\n            await workflowView.insertUser(userInfo);\n        },\n        setUser(userInfo: IUser) {\n            workflowView.setUser(userInfo);\n        },\n        getUser() {\n            return workflowView.getUser();\n        },\n        async listProcess() {\n            return await workflowView.listProcess();    \n        },\n        startWorkflowView(element: HTMLElement) {\n            return workflowView.startWorkflowView!(element);\n        },\n        processListForStartNewProcess(){\n            return workflowView.processListForStartNewProcess();\n        },\n        setProcessToViewModel(process: IProcess | string) {\n            return workflowView.setProcessToViewModel(process);\n        },\n        setIsNewProcess(value: boolean) {\n            workflowView.setIsNewProcess(value);\n        }\n    }\n}\nconst tablist = computed(() => {\n    return (\n        viewModel.studio?.openItems?.map((item) => {\n            const retVal: ITab = {\n                name: item.name,\n                type: item.modelType,\n                id: item.ID,\n                selected: viewModel.studio.currentItem?.ID == item.ID,\n                modified: item.modified?.model,\n                synced: item.synced,\n                cb: {\n                    close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n                    select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n                },\n            };\n            return ref(retVal).value;\n        }) || []\n    );\n});\n\n\n\nasync function goMainPage() {\n    if (viewModel.studio.openItems && viewModel.studio.openItems.some(item => item.checkedOut && item.modified?.model)) {\n        const result = await notification.showConfirm({\n            type: \"warning\",\n            title: `You are leaving the page.`,\n            text: \"Changes you made may not be saved.\",\n            showCloseIcon: true,\n            approveText: \"Confirm\",\n            cancelText: \"Cancel\",\n        });\n        if (result.result == \"cancel\") {\n            return;\n        }\n    }\n    router.push(`/main/applicationList/${viewModel.user!.selectedOrganization!.id}`);\n}\n\nfunction logout() {\n    executor.execute(async () => { await authentication.signOut(); }, { loading: true, noContinueInBgBtn: true });\n}\n\nfunction openCustomPreviewSettingsScreen() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/preview/customPreviewSettings.vue`)),\n        { closable: true, title: \"Custom Preview Settings\", width: \"800px\", height: \"705px\" }\n    );\n    document.getElementById(\"dropdownLivePreviewButton\")!.setAttribute(\"aria-expanded\", \"false\")\n}\n\nfunction hoverLogo() {\n    hoverValue.value = true;\n}\nfunction unHover() {\n    hoverValue.value = false;\n}\n\nfunction setExportType(value: ExportType) {\n    exportAppType.value = value;\n}\n\nasync function checkPreviews() {\n    showLoading.value = true;\n    const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")!.getAttribute(\"aria-expanded\");\n    if (ariaExpanded == \"false\") return;\n    let livePreviewList = await livePreviwUseCase.listLivePreviewInfo(viewModel.studio.appId);\n    showLoading.value = false;\n\n    if (livePreviewList && livePreviewList.length > 0) {\n        const customPreview = livePreviewList.find(lp => lp.type == \"custom\") as ICustomLivePreview;\n        const selectedPreview = livePreviewList.find(lp => lp.selected);\n        previewType.value = selectedPreview!.type;\n        if (!showQRCode.value || streamingLpManager.Status === \"disconnected\") {\n            showQRCode.value = true;\n            const img = document.getElementById(\"qrcode\")!;\n            (img as HTMLImageElement).src = await streamingLpManager.ConnectAndGenerateQR();\n        }\n\n        if (customPreview) {\n            const date = new Date(customPreview.creationInfo.date)\n            customPreviewLastUpdated.value = date.getDate() + \".\" + (date.getMonth() + 1) + \".\" + date.getFullYear() + \" \" + (date.getHours() < 10 ? \"0\" : \"\") + date.getHours() + \":\" + (date.getMinutes() < 10 ? \"0\" : \"\") + date.getMinutes();\n            showCustomPreview.value = true;\n        }\n    } else {\n        showCustomPreview.value = false;\n        previewType.value = \"default\";\n    }\n}\n\nasync function openLivePreviewTab() {\n    const appID = viewModel.studio.appId;\n    const appName = viewModel.studio.appName;\n\n    await executor.execute(async () => {\n        await livePreviwUseCase.createLivePreview({ appID, type: \"default\", selected: true });\n\n        const lpUrl = `../../qui-lp/index.html`;\n\n        await livePreviwUseCase.startLivePreview(appID, lpUrl);            \n    }, { loading: true });\n}\n\nfunction openFeedbackDialog() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/giveUsFeedbackDialog.vue`)),\n        { closable: true, title: \"Give Us Feedback\", width: \"540px\", maxHeight: \"706px\" },\n    );\n}\n\nfunction handleStudioOrCustom(selectedType: string) {\n    if (showCustomPreview.value) {\n        executor.execute(\n            async () => {\n                await livePreviwUseCase.updateSelectedLivePreview(viewModel.studio.appId);\n                previewType.value = selectedType;\n            },\n            { loading: true }\n        )\n    }\n}\n\nfunction publishApplication() {\n    operationState.publishing.value = !operationState.publishing.value;\n    const platformSelection: IPlatformSelection = { web: publishToWeb.value, mobile: publishToMobile.value };\n    const title = platformSelection.web ? platformSelection.mobile ? \"Web and Mobile\" : \"Web\" : platformSelection.mobile ? \"Mobile\" : \"\";\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n        { closable: true, title: \"Publish Modules to \" + title, width: \"800px\", height: \"622px\" },\n        { screenType: \"Deploy\", platformSelection }\n    );\n}\n\nfunction exportAppClickEvent() {\n    if (exportAppType.value == \"create\")\n        downloadApplication();\n    else if (exportAppType.value == \"sync\") {\n        executor.execute(async () => {\n            await exportAppUseCase.syncFiles();\n        }, { loading: false });\n    }\n}\n\nfunction copyExportCommand() {\n    navigator.clipboard.writeText(\"symphony-cli studio-sync\");\n}\n\nfunction downloadApplication() {\n    if (viewModel.user?.selectedOrganization?.accountType == \"premium\") {\n        operationState.downloading.value = !operationState.downloading.value;\n\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n            { closable: true, title: \"Chose Modules to Download\", width: \"800px\", height: \"531px\" },\n            { screenType: \"Export\" }\n        );\n    }\n}\n\nfunction seeProgressDetails(showingTab: \"publish\" | \"download\") {\n    page.openPage(\n        defineAsyncComponent(() => import('../dialogs/publish/publishHistory.vue')),\n        {name : \"Progress Details\"}, {showingTab: showingTab}\n    );\n}\n\nfunction setLatestPublish() {\n    const azure = currentApp.value?.cloud?.azure;\n    const gcp = currentApp.value?.cloud?.plateauGCP;\n\n    if (azure && azure.web?.publishDate) {\n        viewModel.studio.webPublishDate = azure.web.publishDate.toLocaleString();\n        viewModel.studio.webPublishedBy = azure.web.publishedBy.split(\"@\")[0];\n    }\n    if (gcp && gcp.web?.publishDate) {\n        if (!azure?.web?.publishDate || gcp.web.publishDate.getTime() > azure.web.publishDate.getTime()) {\n            viewModel.studio.webPublishDate = gcp.web.publishDate.toLocaleString();\n            viewModel.studio.webPublishedBy = gcp.web.publishedBy.split(\"@\")[0];\n        }\n    }\n\n    if (azure && azure.mobile?.publishDate) {\n        viewModel.studio.mobilePublishDate = azure.mobile.publishDate.toLocaleString();\n        viewModel.studio.mobilePublishedBy = azure.mobile.publishedBy.split(\"@\")[0];\n    }\n    if (gcp && gcp.mobile?.publishDate) {\n        if (!azure?.mobile?.publishDate || gcp.mobile.publishDate.getTime() > azure.mobile.publishDate.getTime()) {\n            viewModel.studio.mobilePublishDate = gcp.mobile.publishDate.toLocaleString();\n            viewModel.studio.mobilePublishedBy = gcp.mobile.publishedBy.split(\"@\")[0];\n        }\n    }\n}\nfunction checkPublishDetails() {\n    setLatestPublish();\n    viewModel.studio.publishId = currentApp.value?.publishmentId;\n    mobilePublishedQRCode();\n}\nfunction mobilePublishedQRCode() {\n    const QRCode = container.resolve<IQRCode>(IQRCode);\n    const canvas = document.getElementById(\"studioapps-qrcode\") as HTMLElement;\n    studioAppLink.value = \"https://studio.onplateau.com/studioapps/\" + viewModel.studio.publishId;\n    QRCode.drawToCanvas(canvas, studioAppLink.value);\n}\nfunction calculateDate(createDate?: string) {\n    if (createDate) {\n        const date = new Date(createDate);\n        if (date) {\n            const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)\n            const hours = parseInt(String(minutes / 60), 10);\n            const days = parseInt(String(hours / 24), 10);\n            const weeks = parseInt(String(days / 7), 10);\n            const months = parseInt(String(weeks / 4), 10);\n            const years = parseInt(String(months / 12), 10);\n            if (years > 0) {\n                return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;\n            } else if (months > 0) {\n                return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;\n            } else if (weeks > 0) {\n                return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;\n            } else if (days > 0) {\n                return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;\n            } else if (hours > 0) {\n                return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;\n            } else if (minutes > 0) {\n                return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;\n            }\n        }\n        return \"few seconds ago\";\n    }\n    else {\n        return \"\";\n    }\n}\nfunction copyToClipboard(element: string, isFromInnerHtml: boolean) {\n    if (isFromInnerHtml) {\n        let text = document.getElementById(element)?.innerHTML;\n        navigator.clipboard.writeText(text!);\n    }\n    else {\n        navigator.clipboard.writeText(element);\n    }\n\n}\n\nfunction redirectToStore(store: string) {\n    switch (store) {\n        case \"apple\": window.open(\"https://apps.apple.com/tr/app/studioapps/id6446583055\", \"_blank\"); break;\n        // case \"google-play\": window.open(\"\", \"_blank\"); break;\n    }\n}\nfunction changeAppLogo() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/appLogoDialog.vue`)),\n        { closable: true, title: \"Select App Image\", width: \"530px\" }, { app: currentApp.value }\n    );\n}\nfunction gotoBackOffice() {\n    //   router.push(\"/backoffice/dashboard\");\n    goToUrl('/backoffice/dashboard');\n}\nfunction goToUrl(url: string) {\n    window.open(url, \"_blank\")\n}\n</script>\n<template>\n    <div>\n        <AnnouncementsBar></AnnouncementsBar>\n        <div class=\"top-menu\">\n            <div class=\"row top-container\">\n                <div class=\"col-lg-1 home-section\" style=\"display: flex\">\n                    <div class=\"corner-container\">\n                        <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\"\n                            :style=\"{ 'backgroundColor': hoverValue ? '#151233' : '', padding: '15px' }\">\n                            <img v-show=\"!hoverValue\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide5-logo\" width=\"24\"\n                                height=\"24\" />\n                            <img v-show=\"hoverValue\" src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\" />\n                        </div>\n                    </div>\n                    <div class=\"vl\"></div>\n                </div>\n\n                <div class=\"col-lg-7 middle-resize\" style=\"display: flex; align-items:center\">\n                    <div class=\"middle-container\">\n                        <div class=\"title-container\">\n                            <div class=\"app-logo-div\" @click=\"changeAppLogo()\">\n                                <img v-if=\"currentApp?.logoInfo && currentApp?.synced\"\n                                    :src=\"`${currentApp.logoInfo.hostUrl}${currentApp.logoInfo.path}?c=${currentApp.logoInfo.updateDate}`\"\n                                    alt=\"app-logo\" class=\"app-logo\" />\n                                <img v-if=\"!currentApp?.logoInfo\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide-logo\"\n                                    class=\"title-logo\" />\n                            </div>\n\n                            <span class=\"title\">{{ currentApp?.name }}</span>\n                        </div>\n                        <div class=\"tabs\">\n                            <Tabs :tabs=\"tablist\" height=\"30px\" ref=\"tabComp\"></Tabs>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col-lg-4 last-resize\">\n                    <div class=\"end-container position-absolute end-0\">\n                        <ChatBot />\n                        <!-- Live Dashboard -->\n                        <button class=\"live-dashboard-button\" :disabled=\"!isFlowProcess\" @click=\"goToPreDashboard\" v-if=\"viewModel.user!.selectedOrganization!.applications.workflow\"><span\n                            class=\"mdi mdi-view-dashboard\"></span> Live Dashboard </button>\n                        <!-- Preview -->\n                        <div style=\"display: flex; margin-right: 12px; flex-direction: column; justify-content: center;\"\n                            :class=\"{ 'button-disabled': !hasQJson || !hasRootPage }\">\n                            <div class=\"preview-button-group\">\n                                <span style=\"margin-top: 7px;\">\n                                    <div type=\"button\" @click=\"openLivePreviewTab()\" class=\"preview-button\">\n                                        <span class=\"preview-play-icon\"><i class=\"mdi mdi-play mdi-20px\"> </i></span>\n                                        <span class=\"preview-text\">Preview</span>\n                                    </div>\n                                </span>\n                                <span style=\"margin-top: 7px;\">\n                                    <div type=\"button\" @click=\"checkPreviews\" class=\"preview-dropdown-button\"\n                                        id=\"dropdownLivePreviewButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                        <!-- <span class=\"preview-dropdown-icon\" :style=\"previewDropdownOpen ? 'transform: rotate(180deg); margin-top: 10px;' : ''\">󰅀</span> -->\n                                        <i class=\"mdi mdi-dots-vertical\" style=\"font-size: 20px\"></i>\n                                    </div>\n                                    <ul class=\"dropdown-menu menu-preview\" id=\"ulDropdown\"\n                                        :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;' + (showLoading ? 'height: 178px;' : showCustomPreview ? 'height: 523px' : showQRCode ? 'height: 485px;' : '')\"\n                                        aria-labelledby=\"dropdownLivePreviewButton\">\n                                        <div v-show=\"!showLoading\">\n                                            <li>\n                                                <div\n                                                    :style=\"{ margin: '25px 0px 0px 101px', width: '72.44px', height: '57.96px' }\">\n                                                    <img src=\"../../../../images/projector.svg\" />\n                                                </div>\n                                            </li>\n                                            <li>\n                                                <span class=\"preview-dropdown-text\">Choose how would you like to preview\n                                                    your\n                                                    application</span>\n                                            </li>\n                                            <hr class=\"preview-first-line\" />\n                                            <li>\n                                                <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                    :active=\"previewType == 'default'\" :type=\"'preview'\"\n                                                    @click=\"handleStudioOrCustom('default')\" />\n                                                <span class=\"preview-menu-studio-text\">Studio Preview</span>\n                                                <br />\n                                                <span class=\"preview-menu-studio-description\">Qui Preview</span>\n                                            </li>\n                                            <hr class=\"preview-second-line\" />\n                                            <li v-if=\"showCustomPreview\">\n                                                <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                    :active=\"previewType == 'custom'\" :type=\"'preview'\"\n                                                    @click=\"handleStudioOrCustom('custom')\" />\n                                                <span class=\"preview-menu-custom-text\">Custom Preview</span>\n                                                <br />\n                                                <span class=\"preview-menu-custom-description\">Last update {{\n                                                    customPreviewLastUpdated }}</span>\n                                                <span id=\"custom-preview\" class=\"edit-custom-preview\"\n                                                    @click=\"openCustomPreviewSettingsScreen()\">\n                                                    <i class=\"mdi mdi-pencil\" style=\"font-size: 16px;\" /> Edit Custom\n                                                    Preview\n                                                </span>\n                                            </li>\n                                            <li v-if=\"!showCustomPreview\">\n                                                <div class=\"preview-menu-items\" @click=\"openCustomPreviewSettingsScreen()\">\n                                                    <span id=\"custom-preview\" class=\"add-custom-preview\">\n                                                        <i class=\"mdi mdi-plus\" /> Add Custom Preview</span>\n                                                </div>\n                                            </li>\n                                            <hr class=\"preview-third-line\" v-show=\"showQRCode\"\n                                                :style=\"showCustomPreview ? 'margin-top: 69px;' : ''\" />\n                                            <li v-show=\"showQRCode || showCustomPreview\">\n                                                <span class=\"qrcode-text\">\n                                                    You can also view your application on <b>mobile</b> by scanning QR code.\n                                                </span>\n                                                <img id=\"qrcode\" class=\"qrcode\" width=\"104\" height=\"104\" />\n\n\n                                                <div class=\"connected-devices-container\">\n                                                    <p class=\"connected-devices-title\">Connected Devices</p>\n                                                    <div class=\"connected-devices\">\n                                                        <ul v-if=\"!relayClients || relayClients.clients.length === 0\"\n                                                            style=\"padding: 0;\">\n                                                            <li\n                                                                style=\"font-size: 75%; display: flex; justify-content: center;\">\n                                                                No clients connected</li>\n                                                        </ul>\n                                                        <ul v-else style=\"padding: 0;\">\n                                                            <li v-for=\"client in relayClients?.clients\"\n                                                                :key=\"client.clientID\" style=\"margin-bottom: 2%;\">\n                                                                <div class=\"client-container\">\n                                                                    <div class=\"client-name\">{{ client.name }}</div>\n                                                                    <i class=\"delete-item mdi mdi-delete-outline\"\n                                                                        @click=\"StopClientRelay(client.clientID)\"></i>\n                                                                </div>\n                                                            </li>\n                                                        </ul>\n                                                    </div>\n                                                </div>\n\n                                            </li>\n                                        </div>\n                                        <div class=\"qr-loading\" v-show=\"showLoading\">\n                                            <img class=\"loading\" src=\"../../../../images/loading.gif\" alt=\"studio-logo\" />\n                                        </div>\n\n                                    </ul>\n                                </span>\n                            </div>\n                            <span class=\"button-tooltip\" style=\"left: -70px; width: 280px\">\n                                To preview your app, you need to set up a start screen created outside of a module <p\n                                    style=\"margin-bottom: 0\">Click <i style=\"background: #50505A\"\n                                        class=\"mdi mdi-dots-horizontal\"></i> on UI Screen item that created outside of a\n                                    module and use \"Set as Start screen\" option. </p>\n                            </span>\n                        </div>\n                        <!-- Export -->\n                        <div style=\"display: flex; flex-direction: column; justify-content: center;\"\n                            :class=\"{ 'button-disabled': isPremiumTooltipActive }\">\n                            <div class=\"export-button-group\">\n                                <span style=\"margin-top: 7px;\">\n                                    <div type=\"button\" @click=\"exportAppClickEvent()\" class=\"export-button\">\n                                        <span style=\"font-style:normal !important\" class=\"export-icon\"><i\n                                                class=\"mdi mdi-export mdi-20px\"> </i></span>\n                                        <span class=\"export-text\">Export</span>\n                                    </div>\n                                </span>\n                                <span style=\"margin-top: 7px;\">\n                                    <div type=\"button\" class=\"preview-dropdown-button\" id=\"dropdownExportButton\"\n                                        data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                        <i class=\"mdi mdi-dots-vertical\" style=\"font-size: 20px\"></i>\n                                    </div>\n                                    <ul v-if=\"viewModel.user?.selectedOrganization?.accountType == 'premium'\"\n                                        class=\"dropdown-menu menu-export\" id=\"ulDropdownExport\"\n                                        :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;' + (exportAppType == 'sync' ? 'height: 509px !important; ' : '')\"\n                                        aria-labelledby=\"dropdownExportButton\">\n                                        <div>\n                                            <li class=\"export-li-first\">\n                                                <div class=\"export-image\">\n                                                    <img src=\"../../../../images/export-box.svg\" />\n                                                </div>\n                                                <span class=\"export-dropdown-text\">Choose how would you like to export your\n                                                    application</span>\n                                            </li>\n                                            <li class=\"export-li-second\">\n                                                <RadioButtonComp class=\"export-radio-button\"\n                                                    :active=\"exportAppType == 'create'\" :type=\"'export'\"\n                                                    @click=\"setExportType('create')\" />\n                                                <span class=\"export-radio-button-header-text\">Export Compiled App\n                                                    Package</span>\n                                                <br />\n                                                <span style=\"top:200px;\"\n                                                    class=\"export-radio-button-description-text\">Download compiled zip file\n                                                    of your project to your device</span>\n                                            </li>\n                                            <li class=\"export-li-third\"\n                                                :style=\"(exportAppType == 'sync' ? 'height: 205px !important; ' : '')\">\n                                                <RadioButtonComp class=\"export-radio-button\"\n                                                    :active=\"exportAppType == 'sync'\" :type=\"'export'\"\n                                                    @click=\"setExportType('sync')\" />\n                                                <span class=\"export-radio-button-header-text\">Export Uncompiled App\n                                                    Models</span>\n                                                <span v-if=\"exportAppType == 'create'\" style=\"top:286px;\"\n                                                    class=\"export-radio-button-description-text\">Sync uncompiled files of\n                                                    your project to your device</span>\n                                                <span @click=\"copyExportCommand()\" v-if=\"exportAppType == 'sync'\"\n                                                    style=\"top:286px;\" class=\"export-radio-button-sync-description-text\">\n                                                    To perform this action, open a Command Prompt in your local app folder\n                                                    and run command below.\n                                                    <p style=\"font-weight: 500; margin-top: 8px\">\n                                                        <span id=\"sym-command\">symphony-cli studio-sync</span>\n                                                        <i @click=\"copyToClipboard('sym-command', true)\"\n                                                            class=\"mdi mdi-content-copy copy-command\">\n                                                            <span class=\"button-tooltip\"\n                                                                style=\"left: 120px; top: 25px; width: 106px; font-style: normal;\">Copy\n                                                                to clipboard</span>\n                                                        </i>\n                                                    </p>\n                                                </span>\n                                                <div v-if=\"exportAppType == 'sync'\"\n                                                    style=\"position:absolute; width: 258px; height:52px; left: 20px; top:366px; margin-top: 20px;\">\n                                                    <div\n                                                        style=\"position: absolute; width: 250px; height:52px; left:0px; top: 0px; background: #F5F5FA; border-radius: 5px;\">\n                                                        <span style=\"font-style:normal !important\"\n                                                            class=\"information-icon\"><i\n                                                                class=\"mdi mdi-information mdi-20px\"> </i></span>\n                                                        <span class=\"export-sync-documantation-text\">\n                                                            If this is your first time exporting, you can contact with\n                                                            Plateau Studio team.\n                                                        </span>\n                                                        <!-- <a class=\"documentationlink\" :href=\"documentationLink\" style=\"text-decoration:none\" target=\"_blank\">See documentation\n                                                        <i class=\"documantation-link-icon mdi mdi-open-in-new\"></i>\n                                                    </a> -->\n                                                    </div>\n                                                </div>\n                                            </li>\n                                            <li class=\"export-li-fourth\">\n                                                <div class=\"export-see-progress-details-div\" @click=\"seeProgressDetails('download')\">\n                                                    <span id=\"see-progress-details\" class=\"see-progress-details-text\">\n                                                        See Progress Details</span>\n                                                </div>\n                                            </li>\n                                        </div>\n                                    </ul>\n\n                                </span>\n                            </div>\n                            <PremiumAccountTooltip class=\"premium-account-tooltip\"></PremiumAccountTooltip>\n                        </div>\n                        <!-- Publish -->\n                        <span style=\"margin-top: 7px; margin-left: 12px;\">\n                            <div :class=\"{ 'button-disabled': !hasModel }\">\n                             