import { CryptoHelper } from "@stechquick/algae/lib/helpers/cryptoHelper";
import { ModuleShareType } from "../../../../common/qCloudTemp/quickCloud";
import { IModel, IModule } from "../model/models";

export interface IMenuItem {
    name: string;
    icon?: string;
    color?: string;
    tooltip?: string;
    style?: string
    enabled?: boolean;
    selectCb: () => void;
}
export interface IActionSingleItem {
    type: "IActionSingleItem";
    icon?: string;
    name: string;
    tooltip?: string;
    enabled?: boolean;
    selectCb?: () => void;
    clicked?: boolean;
}
export interface IActionMenuItem {
    type: "IActionMenuItem";
    name: string;
    icon?: string;
    tooltip?: string;
    style?: string;
    children: Array<IMenuItem> | (() => Array<IMenuItem>);
    clicked?: boolean;
}



export type IActionItem = IActionSingleItem | IActionMenuItem;
export interface ITreeItem<T> {
    id: string;
    name: string;
    typeIcon: ITreeItemIcon;
    orderIndex?: number;
    itemIcons?: Array<ITreeItemIcon>;
    children?: Array<ITreeItem<T>>;
    externalData: T;
    actions?: Array<IActionItem>;
    actionsWithRoot?: boolean;
    unselectable?: boolean;
    cb?: {
        select?: () => void;
        unselect?: () => void;
        unselectableClick?: () => void;
    };
    color?: string;
}

export interface ITreeItemIcon {
    id?: string;
    icon: string;
    text?: string;
    tooltip?: string;
    color?: string;
    fontsize?: string;
    margin?: string;
}
export interface ITreeViewOptions {
    header?: {
        text: string;
        actions?: Array<IActionItem>;
        actionsWithRoot?: boolean;
    };
    reset?: boolean;
}
export interface ICloneFilterOptions<T> {
    items: Array<ITreeItem<T>> | "self";
    filterer?: (item: ITreeItem<T>) => boolean;
}
export interface ITreeView<T> {
    setup(parent: HTMLElement, options?: ITreeViewOptions): void;
    addItem(items: ITreeItem<T> | Array<ITreeItem<T>>, parentId?: string): void;
    getItem(itemId: string): ITreeItem<T> | undefined;
    getItemParent(itemId: string): ITreeItem<T> | undefined;
    getItemByFullPath(fullPath: string): ITreeItem<T> | undefined;
    updateItemName(itemId: string, itemName: string): void;
    updateItemColor(itemId: string, itemColor: string): void;
    setHeader(header: ITreeViewOptions["header"]): void;
    /**
     * @returns deleted item id(s)
     */
    removeItem(itemId: string): Array<string>;
    collapseAll(): void;
    selectItem(itemId: string): void;
    unselectItem(itemId: string): void;
    reset(): void;
    addItemIcon(itemId: string, icon: ITreeItemIcon): void;
    removeItemIcon(itemId: string, icon: ITreeItemIcon): void;
    updateActions(itemId: string): void;
    // updateModuleShareType(itemId: string, itemShareType: ModuleShareType): void;
    cloneFilterTreeItems(options: ICloneFilterOptions<T>): Array<ITreeItem<T>>;
}
export const ITreeView = Symbol.for("ITreeView");
export const ITreeViewInstances = {
    FileExplorer: Symbol.for("FileExplorer"),
};
