import { injectable } from "tsyringe";

const bootstrap = require("bootstrap");

@injectable()
export class TooltipImpl {
    public startTooltip() {
        const tooltipTriggerList = Array.from(document.querySelectorAll('[data-toggle="tooltip"]'));
        tooltipTriggerList.forEach((tooltipTriggerEl: Element) => {
            // Initialize the tooltip with the 'hover' trigger
            const tooltipInstance = new bootstrap.Tooltip(tooltipTriggerEl, {
                delay: { show: 500, hide: 0 },
                trigger: 'hover'
            });
    
            if (tooltipTriggerEl instanceof HTMLElement) {
                tooltipTriggerEl.addEventListener('mouseleave', function() {
                    tooltipInstance.hide();
                });
            }
        });
    }
    
}
