wModel.user!.selectedOrganization?.creatorUserId === userId;\n    const isOrganizationGroupOwner = () =>  viewModel.user!.organizationGroups && viewModel.user!.organizationGroups.length > 0;\n    const isOrganizationInGroup = viewModel.user!.selectedOrganization?.organizationGroupId ? true : false;\n    \n    function openPremiumAccountForm() {\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`./premiumAccountForm.vue`)),\n            { closable: true, image: {src: \"https://stechq.github.io/cdn/Idetemplates/plateauLogo-premium.svg\", width: \"134px\", height: \"40px\"},\n              width: \"1022px\", maxHeight: \"688px\", customStyle: {headerStyle: \"background: #1B4874; border-bottom: 1px solid #E3E3E3; padding: 20px; height: 80px; display: flex; align-items: center;\", closeIcon: \"color: #FFFFFF\"} }\n        );\n    }\n    function openOrgGroupRequestDialog() {\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../../components/dialogs/organization/joinOrganizationGroupDialog.vue`)),\n            { closable: true, title: \"Join Group Request\", width: \"658px\", height: \"549px\" },\n        );\n    }\n</script>\n<template>\n    <div class=\"premium-account-dropdown-menu\">\n        <div>\n            <img src=\"../../../../../images/plateauLogo-premium.svg\" style=\"margin-bottom: 48px\"/>\n            <p v-if=\"isOrganizationOwner(viewModel.user!.userId)\" style=\"font-size: 15px; color: #D5D5D5\">This feature is  Premium Only.  You can apply for Premium version.</p>\n            <p v-else style=\"font-size: 15px; color: #D5D5D5\">This feature is Premium Only. Contact your organization owner for Premium version.</p>\n        </div>\n        <div style=\"border-top: 1px solid #F5F5F5\">\n            <p style=\"color: #F1AE00;font-size: 14px\">Plateau Studio Premium</p>\n            <p  style=\"font-size: 12px\">You can create the best user development on our unique dev experience. Access advanced features such as export your application, collaboration with your team, and more.</p>\n        </div>\n        <div v-if=\"isOrganizationOwner(viewModel.user!.userId) || isOrganizationGroupOwner()\" style=\"text-align: center;\">\n            <button class=\"try-premium-button\" @click=\"openPremiumAccountForm()\">TRY PREMIUM NOW</button>\n            <button v-if=\"!isOrganizationInGroup && isOrganizationOwner(viewModel.user!.userId)\" class=\"join-group-button\" @click=\"openOrgGroupRequestDialog()\">JOIN A GROUP</button>\n        </div>\n    </div>\n\n</template>\n\n<style>\n    \n    .premium-account-dropdown-menu {\n        width: 290px;\n        background: #1B4874;\n        border-radius: 10px;\n        border: 1px;\n        position: absolute;\n        color: #FFFFFF;\n        font-size: 11px;\n        z-index: 1;\n        text-align: center;\n    }\n    .premium-account-dropdown-menu div {\n        padding: 24px;\n    }\n    .try-premium-button {\n        background: #FFFFFF;\n        border: none;\n        border-radius: 4px;\n        padding: 8px 0px;\n        color: #151233;\n        font-size: 14px;\n        font-weight: 500;\n        margin-bottom: 16px;\n        width: 100%;\n    }\n    .join-group-button {\n        background: transparent;\n        border: 1px solid #FFFFFF;\n        border-radius: 4px;\n        padding: 8px 0px;\n        color: #FFFFFF;\n        font-size: 14px;\n        font-weight: 500;\n        width: 100%;\n    }\n</style>\n"],"sourceRoot":""}]);
