function setLatestPublish() {
    const azure = currentApp.value?.cloud?.azure;
    const gcp = currentApp.value?.cloud?.plateauGCP;

    if (azure && azure.web?.publishDate) {
        viewModel.studio.webPublishDate = azure.web.publishDate.toLocaleString();
        viewModel.studio.webPublishedBy = azure.web.publishedBy.split("@")[0];
    }
    if (gcp && gcp.web?.publishDate) {
        if (!azure?.web?.publishDate || gcp.web.publishDate.getTime() > azure.web.publishDate.getTime()) {
            viewModel.studio.webPublishDate = gcp.web.publishDate.toLocaleString();
            viewModel.studio.webPublishedBy = gcp.web.publishedBy.split("@")[0];
        }
    }

    if (azure && azure.mobile?.publishDate) {
        viewModel.studio.mobilePublishDate = azure.mobile.publishDate.toLocaleString();
        viewModel.studio.mobilePublishedBy = azure.mobile.publishedBy.split("@")[0];
    }
    if (gcp && gcp.mobile?.publishDate) {
        if (!azure?.mobile?.publishDate || gcp.mobile.publishDate.getTime() > azure.mobile.publishDate.getTime()) {
            viewModel.studio.mobilePublishDate = gcp.mobile.publishDate.toLocaleString();
            viewModel.studio.mobilePublishedBy = gcp.mobile.publishedBy.split("@")[0];
        }
    }
}
