    studioAppLink.value = "https://studio.onplateau.com/studioapps/" + viewModel.studio.publishId;
    QRCode.drawToCanvas(canvas, studioAppLink.value);
}
function calculateDate(createDate?: string) {
    if (createDate) {
        const date = new Date(createDate);
        if (date) {
            const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)
            const hours = parseInt(String(minutes / 60), 10);
            const days = parseInt(String(hours / 24), 10);
            const weeks = parseInt(String(days / 7), 10);
            const months = parseInt(String(weeks / 4), 10);
            const years = parseInt(String(months / 12), 10);
            if (years > 0) {
                return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;
            } else if (months > 0) {
                return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;
            } else if (weeks > 0) {
                return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;
            } else if (days > 0) {
                return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;
            } else if (hours > 0) {
                return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;
            } else if (minutes > 0) {
                return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;
            }
        }
        return "few seconds ago";
    }
    else {
        return "";
    }
}
function copyToClipboard(element: string, isFromInnerHtml: boolean) {
    if (isFromInnerHtml) {
        let text = document.getElementById(element)?.innerHTML;
        navigator.clipboard.writeText(text!);
    }
    else {
        navigator.clipboard.writeText(element);
    }

}

