import { CryptoHelper } from "@stechquick/algae/lib/helpers/cryptoHelper";
import { AllCreateObjectTypes, ModuleShareType } from "../../../../common/qCloudTemp/quickCloud";
import { IApplication, IFolder, IModel, IModule } from "../model/models";
import { ITreeItem } from "../presentation/ITreeView";


/**
 * File Explorers treeview external data type. this is used for strongly type info inside treeview externalData
 */
export type IFEExternalData = IFolder | IModule | IModel;
export class TreeItemFolderDict <TExternalData = IFEExternalData> {
    private static folderIdPrefix = "this_is_folder_";
    public static isFolder(itemId: string) {
        return itemId.startsWith(TreeItemFolderDict.folderIdPrefix);
    }
    public static createFolderID() {
        return TreeItemFolderDict.folderIdPrefix + CryptoHelper.CreateGuid();
    }

    public static createPseudoFolder(ID: string, name: "Styled Components" | "UI Settings", owner: IApplication | IModule, path: string) {
        const folder: IFolder = {ID, name, objectType: "folder",owner,path};
        return folder;
    }
    public static createITreeItemForFolder<TExternalData>(folder: IFolder): ITreeItem<TExternalData> {
        return { id: folder.ID, name: folder.name, typeIcon: { icon: 'mdi mdi-folder-open' }, actions: [], children: [], externalData: folder as TExternalData};
    }

    private rootFolders: Array<ITreeItem<TExternalData>> = []; public getRootFolders() { return this.rootFolders; }
    private folderPathDict: Record<string, ITreeItem<TExternalData> | undefined> = {};
    public addFolder(owner: IModule | IApplication, path: string, options: { children: ITreeItem<TExternalData> | undefined, getExistingFolder?: (fullPath: string ) => ITreeItem<TExternalData> | undefined }): { folder: ITreeItem<TExternalData>, newFolders?: Array<ITreeItem<TExternalData>> } | undefined {
        if (path == "/") { return undefined; }
        options.getExistingFolder = options.getExistingFolder || (() => undefined);

        path = path.endsWith("/") ? path.substring(0, path.length - 1) : path; // /a/b/ -> /a/b
        const lastSlash = path.lastIndexOf("/")!;
        const name = path.substring(lastSlash + 1);
        const id = TreeItemFolderDict.createFolderID();
        const parentPath = path.substring(0, lastSlash) || "/";

        const selfFullPath = (owner.objectType == "module" ? "/" + owner.name : "") + path;
        if (options.getExistingFolder(selfFullPath)) { return undefined; } // external existing folders should be threated as root like.

        const isNew = this.folderPathDict[path] == undefined;
        const externalData: IFolder = { ID: id, name, objectType: "folder", path: parentPath, owner };
        const folder: ITreeItem<TExternalData> = this.folderPathDict[path] || TreeItemFolderDict.createITreeItemForFolder(externalData);

        if (options.children) {
            folder.children = folder.children || [];
            folder.children.push(options.children);
        }

        let newFolders: Array<ITreeItem<TExternalData>> | undefined;
        if (isNew) {
            const parentFolder = lastSlash == 0 ? undefined : this.addFolder(owner, parentPath, { children: folder, getExistingFolder: options.getExistingFolder });
            this.folderPathDict[path] = folder;
            !parentFolder && this.rootFolders.push(folder);
            newFolders = parentFolder?.newFolders || [];
            newFolders.push(folder);
        }

        return { folder, newFolders };
    }
}

export const FileExplorerConstants = {
    uiSettingsID: "uisettings-001",
    uiAssetListID: "uiassetlist-001",
};

export interface IFileExplorer {
    addNewItem(item: IModule | IModel | IFolder, color?: string): void;
    removeItem(itemID: string): { deletedItemIds: string[] };
    updateItemName(itemId: string, itemName: string): void;
    updateItemColor(itemId: string, itemColor: string): void;
    folderExists(fullPath: string, ownerId: string): boolean;
    // updateModuleShareType(itemId: string, itemShareType: ModuleShareType): void;
    loadFileExplorer(rootDiv: HTMLElement): Promise<void>;
    selectItem(itemID: string): void;
    unselectItem(itemID: string): void;
    createAssetListModel({ name, modelBody }:{name: string, modelBody: string}): void;
    onNewItemSelect(ownerId: string, createObjectType: AllCreateObjectTypes, parentItem: ITreeItem<IFEExternalData> | undefined, duplicateFrom?: IModel): Promise<void>;
    findFolder(folderId: string): IFolder | undefined;
    findTreeItem(itemId: string): ITreeItem<IFEExternalData> | undefined;
    createUIAssetList(): void;

    cloneFilterItems(items: Array<ITreeItem<IFEExternalData>> | "self", filterer: (item: ITreeItem<IFEExternalData>) => boolean): Array<ITreeItem<IFEExternalData>>;
    cloneFilterFolderItems(folderId: string, filterer: (item: ITreeItem<IFEExternalData>) => boolean): Array<ITreeItem<IFEExternalData>> | undefined;
}
export const IFileExplorer = Symbol.for("IFileExplorer");