import { IModelCheckout } from "../../../../common/qCloudTemp/quickCloud";
import { IModel, IModule } from "../model/models";
import { INavItem } from "../model/ui/INavItem";
import { IInsertTextOptions } from "../objects/editors/IEditor";

export interface IStudio {
    attach(element: HTMLDivElement): void;
    openItem(item: IModel, options?: { fromHistory?: boolean; noFocus?: boolean; }): Promise<void>;
    openHistoryItem(historyID: string): Promise<void>;
    detach(): void;
    closeItem(item: IModel, options?: { silent?: boolean,  gotMoreToClose?: boolean; }): void;
    openApp(appId: string): Promise<void>;

    checkout(item: IModel): Promise<void>;
    undoCheckout(item: IModel): Promise<void>;
    save(item: IModel): Promise<void>;
    play(item: IModel): Promise<void>;
    checkin(item: IModel, options: { shortComment: string; longComment?: string; version: "major"| "minor" |""; }): Promise<void>;
    release(item: IModel): Promise<void>;

    insertTextToEditor(text: string, options: IInsertTextOptions): Promise<void>

    updateCurrentItem(): Promise<void>;
    reflectCurrentItem(): Promise<void>;
    isOpen(modelId: string): boolean;
    isCurrent(modelId: string): boolean;
    isCheckedOutByMe(checkouts?: IModelCheckout[]): boolean;
    getCheckedOutInfo(checkouts?: IModelCheckout[]): IModelCheckout | undefined;


    attachModuletoApplication(module: IModule, applicationID: string, useVersion?: boolean, version?: string): Promise<void>;
    detachModuleFromApplication(module: IModule, applicationID: string, updateModule?: boolean, version?: string): Promise<void>;

    nav_addNavItems(addItems: Array<INavItem>): void;
    nav_selectItem(navItem: INavItem): Promise<void>;
    nav_unselect(): Promise<void>;
    nav_removeNavItems(removeItems: Array<INavItem>): void;
}
export const IStudio = Symbol.for("IStudio");