/../../../domain/core/diContainer';\nimport { IQJsonAdditionals } from '../../../../../../common/qCloudTemp/quickCloud';\nimport { ITopMenuButton } from '../../../../domain/model/ui/ITopMenuButton';\nimport { IModelCacheUseCase } from '../../../../domain/useCase/IModelCacheUseCase';\nimport { IModelUseCase } from '../../../../domain/useCase/IModelUseCase';\nimport ModelWarning from '../modelWarning.vue'\n\ninterface ITopMenuProp {\n    expandOrCollapseCB?:() => void;\n}\n\nconst props = defineProps<ITopMenuProp>();\nconst studio = container.resolve<IStudio>(IStudio);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst historyUseCase = container.resolve<IHistoryUseCase>(IHistoryUseCase);\nconst modelCacheUseCase = container.resolve<IModelCacheUseCase>(IModelCacheUseCase);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\n\nconst currentModelSize = computed(() => viewModel.studio.currentItem?.size);\nconst modelSizeWarningLimit = ref(1024*1024);\nconst modelSizeCautionLimit = ref(3*1024*1024);\nconst saveDisabled = computed(() => !viewModel.studio.currentItem?.modified.model || !viewModel.studio.currentItem.checkedOut);\nconst isShowViewModeWarning = computed(() => !viewModel.studio.currentItem?.checkedOut && !viewModel.studio.currentItem?.isUnmodifiable);\nconst isWorkingFromOthers = computed(() => !viewModel.studio.currentItem?.checkedOut && viewModel.studio.currentItem?.checkoutInfo)\nconst cacheTime = computed<{ type: \"resolved\", time: string } | { type: \"resolving\" } | undefined>(() => {\n    const currentItem = viewModel.studio.currentItem;\n    if (currentItem?.notModifiableByOthers()) { return undefined; }\n    switch (currentItem?.cacheInfo?.type) {\n        case \"resolved\":\n            const time = currentItem.cacheInfo.time;\n            return { type: \"resolved\", time: time.toLocaleString()};\n        case \"resolving\": return { type: \"resolving\" };\n        default: return undefined;\n    }\n})\n\nconst showModelWarning = ref(false);\nconst modelWarningType = computed(() => currentModelSize.value &&  currentModelSize.value > modelSizeCautionLimit.value ? \"caution\" : \"warning\" );\nconst modelWarningTitle = computed(() => modelWarningType.value == \"warning\" ? \"Potential Performance Impact\" : \"Critical Performance Impact\");\nconst modelWarningText = computed(() => modelWarningType.value == \"warning\" ? \"The size of your model is larger than expected. While this won't affect your saving process, it may lead to performance issues. We recommend dividing <a class='block-comp-doc' target='_blank' href='https://docs.onplateau.com/UI-Screens/Components/Application-Components/block-component'>Block Components</a> and optimizing the model size for better performance.\" \n                                                                            : \"The size of your model is too big, which may significantly impact performance. We recommend dividing <a class='block-comp-doc' target='_blank' href='https://docs.onplateau.com/UI-Screens/Components/Application-Components/block-component'>Block Components</a> and optimizing the model size to prevent potential slowdowns.\");\nfunction Save() {\n    if (saveDisabled.value) { return; }\n\n    executor.execute(async () => {\n        await studio.save(viewModel.studio.currentItem!)\n    }, { loading: true });\n\n}\nfunction History() {\n    executor.execute(async () => {\n        const item = viewModel.studio.currentItem!;\n        await historyUseCase.openHistoryDialog(item.ID, item.name);\n    }, { loading: true });\n}\nasync function Delete() {\n    await objectUseCase.deleteObject(viewModel.studio.currentItem?.ID!);\n}\nasync function reload() {\n    modelCacheUseCase.retrieveModelBodies([viewModel.studio.currentItem!.ID], { skipTimeCheck: true, retrieveDependents: true });\n}\nfunction AddFont(){\n    executor.execute(async () => {\n        dialog.showDialog(defineAsyncComponent(() => import(\"../../../vue3/components/dialogs/studio/addFont.vue\")), { closable: true, height: '476px', width: '604px',title: 'Add Font' /*viewModel.studio.currentItem?.name*/, closeOnOutClick: true}, {item: viewModel.studio.currentItem});\n    }, { loading: true });\n}\n\nfunction Checkin() {\n    executor.execute(async () => {\n        dialog.showDialog(defineAsyncComponent(() => import(\"../../../vue3/components/dialogs/studio/savePlus.vue\")), { closable: true, height: '476px', width: '604px',title: 'Stop Working' /*viewModel.studio.currentItem?.name*/}, {item: viewModel.studio.currentItem});\n    }, { loading: true });\n}\nfunction Checkout() {\n    executor.execute(async () => {\n        await studio.checkout(viewModel.studio.currentItem!)\n    }, { loading: true });\n}\nfunction convertToCustomComp() {\n    executor.execute(async () => {\n        const modelAdditionals: IQJsonAdditionals = { qjsonType: 'quickComponent' }\n        const modelUseCase = container.resolve<IModelUseCase>(IModelUseCase);\n        await modelUseCase.updateModel(viewModel.studio.currentItem!.ID, { name: viewModel.studio.currentItem!.name, modelAdditionals }, { showNotification: true });\n    }, { loading: true });\n}\nfunction topMenuButton(button: ITopMenuButton) {\n    if (button.disabled) { return; }\n    button.onSelect();\n}\n\nonMounted(() => {\n    tooltip.startTooltip();\n    watch(viewModel.studio.topMenu!, () => {\n        tooltip.startTooltip();\n    });\n});\nfunction calculateTime(createDate?: string) {\n    if(createDate) {\n        const date = new Date(createDate);\n        if(date) {\n            const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)\n            const hours = parseInt(String(minutes / 60), 10);\n            const days = parseInt(String(hours / 24), 10);\n            const weeks = parseInt(String(days / 7), 10);\n            const months = parseInt(String(weeks / 4), 10);\n            const years = parseInt(String(months / 12), 10);\n            if(years > 0) {\n                return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;\n            } else if(months > 0) {\n                return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;\n            } else if(weeks > 0) {\n                return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;\n            } else if(days > 0) {\n                return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;\n            } else if(hours > 0) {\n                return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;\n            } else if(minutes > 0) {\n                return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;\n            }\n        }\n        return \"few seconds ago\";\n    }\n    else {\n        return \"\";\n    }\n\n}\nfunction expandOrCollapse(e: any) {\n    if(props.expandOrCollapseCB) props.expandOrCollapseCB();\n\n    const classList = e.target.classList;\n    if(classList.contains(\"mdi-arrow-expand\")) {\n        classList.remove(\"mdi-arrow-expand\");\n        classList.add('mdi-arrow-collapse')\n    } else { \n        classList.remove(\"mdi-arrow-collapse\");\n        classList.add('mdi-arrow-expand');\n    }\n}\nfunction displayModelWarning() {\n    showModelWarning.value = !showModelWarning.value;\n}\nfunction closeWarning() {\n    showModelWarning.value = false;\n}\n</script>\n<template>\n    <div class='topMenuDiv'>\n        <div :class=\"['topMenuDivPartition', isShowViewModeWarning || viewModel.studio.currentItem?.isUnmodifiable ? 'col-3': 'col-2']\">\n            <div v-show=\"!viewModel.studio.currentItem?.isUnmodifiable && viewModel.studio.currentItem?.checkedOut\" :class=\"currentModelSize && currentModelSize > modelSizeWarningLimit ? 'save-with-warning-div' : ''\">\n                <div style=\"background: #FFFFFF; border-radius: 2px;\"  @click=\"Save()\" data-toggle=\"tooltip\" title=\"Save\" placement='bottom'>\n                    <i class='mdi mdi-content-save-outline topMenuIcons' :class=\"{ 'disabled-item': saveDisabled, disabled: saveDisabled, 'save-with-warning':  currentModelSize && currentModelSize > modelSizeWarningLimit}\"></i>\n                 </div>\n                 <div v-show=\"currentModelSize && currentModelSize > modelSizeWarningLimit\" @click=\"displayModelWarning\" style=\"display: flex; justify-content: center; align-items: center;\">\n                    <i class=\"mdi mdi-alert topMenuIcons\" :style=\"{ color: modelWarningType == 'caution' ? '#FD454C' : '#F3CA40'}\" style=\"font-size: 16px\"></i>\n                </div>\n                <ModelWarning v-if=\"showModelWarning\" :title=\"modelWarningTitle\" :text=\"modelWarningText\" :type=\"modelWarningType\" @close=\"closeWarning\"></ModelWarning>\n            </div>\n\n            <div v-show=\"!viewModel.studio.currentItem?.isUnmodifiable\" @click=\"History()\" data-toggle=\"tooltip\" title=\"History\" placement='bottom'>\n                <i class='mdi mdi-history topMenuIcons'></i>\n            </div>\n            <div v-show=\"!viewModel.studio.currentItem?.version && viewModel.studio.currentItem?.checkedOut\" @click=\"Delete()\" data-toggle=\"tooltip\" title=\"Delete\" placement='bottom'>\n                <i class='mdi mdi-delete topMenuIcons'></i>\n            </div>\n            <div v-for=\"item in viewModel.studio.topMenu?.additionalButtons\" :key=\"item.ID\" data-toggle=\"tooltip\" :title=\"item.name\" placement='bottom' @click=\"topMenuButton(item)\">\n                <i class='mdi topMenuIcons' :class=\"item.iconClass\"></i>\n            </div>\n            <!-- <div v-show=\"viewModel.studio.currentItem?.modelType=='qjson' && (viewModel.studio.currentItem.additionals as IQJsonAdditionals).qjsonType == 'qjson' && viewModel.studio.currentItem.usageType!='appSettings' \" @click=\"convertToCustomComp()\" -->\n            <div v-show=\"viewModel.studio.currentItem?.modelType=='qjson' && (viewModel.studio.currentItem.additionals as IQJsonAdditionals).qjsonType == 'qjson' && viewModel.studio.currentItem.usageType!='appSettings' && !viewModel.studio.currentItem?.version && viewModel.studio.currentItem?.checkedOut\" @click=\"convertToCustomComp()\"\n                data-toggle=\"tooltip\"\n                title=\"Convert to Block Component\"\n                placement='bottom'>\n                <i style=\"color: #151233;\" class='mdi mdi-swap-horizontal topMenuIcons'></i>\n            </div>\n            <div v-for=\"item in viewModel.studio.topMenu?.dynamicButtons\" :key=\"item.ID\" @click=\"topMenuButton(item)\"\n                data-toggle=\"tooltip\" :title=\"item.tooltip\" placement='bottom'\n                class=\"topMenuTexts\"\n                :class=\"{ 'disabled-item': item.disabled, disabled: item.disabled }\">\n                {{item.name}}\n            </div>\n            <div v-if=\"cacheTime?.type=='resolving'\" data-toggle=\"tooltip\" title=\"Loading...\" placement='bottom'>\n                <i class='mdi mdi-timer topMenuIcons'></i>\n            </div>\n            <div v-if=\"cacheTime?.type == 'resolved'\" @click=\"reload()\">\n                <i class='mdi mdi-refresh topMenuIcons' data-toggle=\"tooltip\" title=\"Reload\" placement='bottom'></i>\n                <span class=\"last-cached-time\">Cached {{ calculateTime(cacheTime.time) }}</span>\n            </div>\n        </div>\n\n        <div v-if=\"isShowViewModeWarning && !isWorkingFromOthers\" class=\"topMenuDivPartition col-3 viewModeWarning\">\n            <i class='mdi mdi-alert'\n                style=\"color:#F3CA40; font-size: 20px;\"></i>\n            <span class=\"viewModeTextFonts\" style=\"color:#616161; margin-left: 5px; margin-top: 4px;\">You are in <span\n                    style=\"font-weight:bold;\">Viewing mode</span>. To edit the page, you need to </span>\n            <div style=\"display: inline-flex; padding-left: 12px; cursor: pointer;\" @click=\"Checkout()\">\n                <i style=\"font-size: 16px; margin-top: 2px;\" class=\"mdi mdi-circle-edit-outline warningCheckoutBtn\"></i>\n                <span\n                    style=\"color:#151233;font-weight:bold; font-style:normal; font-size: 14px; margin-top: 4px; margin-left:2px\">\n                    Start Working\n                </span>\n            </div>\n        </div>\n        <div v-else-if=\"isShowViewModeWarning && isWorkingFromOthers\" class=\"topMenuDivPartition col-3 viewModeWarning workingFromOthersInfo\">\n            <div style=\"display: block; background-color: #F5F5FA; padding: 2px 16px; border-radius: 4px; height: 32px;\">\n                <img src=\"../../../../images/profile-image-dark-in-menu.svg\" alt=\"\" style=\"width: 20px; height: 20px;\"/>\n                <span class=\"viewModeTextFonts\" style=\"color:#616161; margin-left: 5px; margin-top: 4px;\">\n                    <span v-if=\"viewModel.studio.currentItem?.checkoutInfo?.user.name\" style=\"font-weight:bold;\">{{viewModel.studio.currentItem?.checkoutInfo?.user.name}} {{viewModel.studio.currentItem?.checkoutInfo?.user.lastName}}</span> \n                    <span v-else style=\"font-weight:bold;\">{{viewModel.studio.currentItem?.checkoutInfo?.user.email}}</span> \n                        is currently working on this page\n                </span>\n            </div>\n        </div>\n        <div :class=\"['topMenuDivPartition', isShowViewModeWarning || viewModel.studio.currentItem?.isUnmodifiable ? 'col-3': 'col-2']\" style=\"justify-content: flex-end;\">\n            <div v-if=\"!viewModel.studio.currentItem?.isUnmodifiable && !isShowViewModeWarning\" style=\"display: inline-flex; padding-right: 18px; cursor: pointer;\" @click=\"Checkin()\">\n                <i style=\"font-size: 16px; margin-top: 2px; margin-left: 4px;\" class=\"mdi mdi-checkbox-marked-circle-outline warningCheckoutBtn\"></i>\n                <span\n                    style=\"color:#151233;font-weight:bold; font-style:normal; font-size: 14px; margin-top: 4px; margin-left:2px\">\n                    Stop Working\n                </span>\n            </div>\n            <i class='topMenuIcons mdi mdi-arrow-expand' \n                style=\"padding: unset; padding-right: 10px;\" @click=\"expandOrCollapse\">\n           </i>\n        </div>\n\n    </div>\n</template>\n<style scoped>\n.disabled-item.disabled {\n    cursor: initial;\n    color: lightgrey;\n}\n\n.plateau-tooltip>.tooltip-inner {\n    background-color: red;\n}\n\n.topMenuDiv {\n    display: flex;\n    background-color: #FFFFFF;\n    flex-direction: row;\n    width: 100%;\n    height: 40px;\n    padding-top: 5px;\n    flex: 0 0 auto;\n}\n.save-with-warning-div {\n    display: flex;\n    align-items: center;\n    border: 1px solid #151233;\n    background: #151233;\n    border-radius: 4px;\n    padding: 2px 0px 2px 2px;\n    margin: 0px 0 4px 8px;\n    height: 32px;\n    position: relative;\n}\n.save-with-warning {\n    padding: 0 4px !important;\n    height: 28px;\n    display: block;\n}\n.topMenuIcons {\n    color: #151233;\n    font-size: 20px;\n    cursor: pointer;\n    padding: 6px;\n}\n.disabled-item.disabled.topMenuTexts {\n    pointer-events: none;\n}\n.topMenuTexts {\n    color: #151233;\n    /* font-size: 21px; */\n    cursor: pointer;\n    /* padding: 6px; */\n    margin:0px 5px 0px 5px;\n    user-select: none;\n}\n\n.viewModeWarning {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    background-color: #F5F5FA;\n    height: 32px;\n    padding: 4px 16px;\n    width: 536px;\n    min-width: 536px;\n    border-radius: 4px;\n    top: 4px;\n}\n\n.workingFromOthersInfo {\n    background-color: #FFFFFF;\n    justify-content: center;\n}\n\n.warningCheckoutBtn {\n    color: #151233;\n}\n\n.viewModeTextFonts {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14px;\n    color: #616161;\n    text-align: left;\n    font-family: 'Roboto';\n    letter-spacing: 0px;\n    opacity: 1;\n}\n\n.topMenuDivPartition {\n    display: flex;\n}\n.topMenuDivPartition.col-3 {\n    width: 33.3%;\n    flex: 1 1 auto;\n}\n\n.topMenuDivPartition.col-2 {\n    width: 50%;\n}\n.last-cached-time {\n    font-size: 14px; \n    vertical-align: text-bottom;\n    color: #424242;\n}\n</style>\n<style>\n    .block-comp-doc,\n    .block-comp-doc:hover {\n        color: #FFFFFF;\n    }\n</style>"],"sourceRoot":""}]);
